<template>
  <div class="page">
    <div class="page__content">
      <div class="page__header">
        <h1 class="page__title">
          Настройки профиля
        </h1>
      </div>
      <div class="user-profile">
        <div class="user-profile__left-col">
          <div class="user-profile__photo" @click="$refs.file.click()">
            <svg class="navbar__logo" v-if="!form.avatar.url">
              <use href="@/assets/img/sprite.svg#icon-anonymous-avatar"></use>
            </svg>
            <img v-if="form.avatar.url" :src="form.avatar.url" alt />
            <input
              type="file"
              ref="file"
              @change="onUploadAvatar"
              style="display: none"
              accept="image/jpeg,image/png,image/gif"
            />
            <div class="user-profile__update-photo">
              обновить<br />
              фотографию
            </div>
          </div>

          <div class="user-profile__status mt-10 ">пользователь</div>
          <div class="user-profile__group-btn">
            <button
              @click="$router.push('/resident-profile')"
              class="button-warning"
              v-if="
                !isResident && !isPartner && !isAdministrator && isAuthenticated
              "
            >
              <svg>
                <use href="@/assets/img/sprite.svg#icon-user-plus"></use>
              </svg>
              Стать арт-резидентом
            </button>
            <button
              @click="$router.push('/partner-profile')"
              class="button-primary mt-10"
              v-if="
                !isResident && !isPartner && !isAdministrator && isAuthenticated
              "
            >
              <svg>
                <use href="@/assets/img/sprite.svg#icon-user-partner"></use>
              </svg>
              Стать бизнес-Партнёром
            </button>
          </div>
        </div>
        <form class="user-profile__form" @submit.prevent>
          <v-input
            :label="'Логин'"
            :error="$v.form.username.$error"
            v-model="$v.form.username.$model"
            :text-error="'введите логин'"
            require-input
          />
          <v-input
            :label="'Имя'"
            :error="$v.form.firstname.$error"
            v-model="$v.form.firstname.$model"
            :text-error="'введите имя'"
            require-input
          />
          <v-input
            :label="'Фамилия'"
            :error="$v.form.lastname.$error"
            v-model="$v.form.lastname.$model"
            :text-error="'введите фамилию'"
            require-input
          />
          <v-input
            :label="'Телефон'"
            :error="$v.form.phone.$error"
            v-model="$v.form.phone.$model"
            :mask="'+7 (###) ###-##-##'"
            :text-error="'введите телефон'"
            read-only
            require-input
          />
          <v-input
            :label="'E-mail'"
            :error="$v.form.email.$error"
            v-model="$v.form.email.$model"
            :text-error="'введите e-mail'"
            require-input
          />
          <v-input :label="'Профиль ВКонтакте'" v-model="form.vk_profile" />
          <div class="user-profile__form-group-btn">
            <button class="button-primary" @click.prevent="onUpdateUser">
              сохранить
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
import { required, minLength, email } from "vuelidate/lib/validators";
import VInput from "../components/vInput";
import { mapGetters } from "vuex";

export default {
  name: "UserProfile",
  components: { VInput },
  data() {
    return {
      form: {
        id: 0,
        username: "",
        firstname: "",
        lastname: "",
        phone: "",
        email: "",
        vk_profile: "",
        avatar: {}
      }
    };
  },
  validations: {
    form: {
      username: {
        required
      },
      firstname: {
        required
      },
      lastname: {
        required
      },
      phone: {
        required,
        minLength: minLength(18)
      },
      email: {
        required,
        email
      }
    }
  },
  methods: {
    // setAvatar() {
    //   this.fileAvatar = this.$refs.file.files[0];
    //   this.urlAvatar = URL.createObjectURL(this.fileAvatar);
    // },

    onUploadAvatar() {
      let formData = new FormData();
      formData.append("file", this.$refs.file.files[0]);

      axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/user/upload-avatar`,
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          }
        )
        .then(result => {
          this.form.avatar = result.data[0];
        })
        .catch(error => {
          console.log(error);
        });
    },
    onUpdateUser() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        this.$store
          .dispatch("user/USER_UPDATE", qs.stringify(this.form))
          .then(() => this.$toasted.global.success("Данные обновлены"))
          .catch(error => {
            error.data.forEach(item => {
              this.$toasted.global.error(item.message);
            });
          });
      }
    }
  },
  mounted() {
    const user = this.$store.getters["user/getUser"];
    this.form = {
      ...user,
      phone: user.phone ? String(user.phone).slice(1) : ""
    };
  },
  computed: {
    ...mapGetters("user", ["isPartner", "isResident", "isAdministrator"]),
    ...mapGetters("auth", ["isAuthenticated"])
  }
};
</script>

<style scoped></style>
