<template>
  <div class="v-input" :class="{ 'v-input--error': error }">
    <input
      type="text"
      :id="id"
      :value="value"
      v-mask="mask"
      @input="$emit('input', $event.target.value)"
      :readonly="readOnly"
    />
    <label :for="id" v-show="!error">
      {{ label }}
      <span class="v-input__require" v-show="requireInput">*</span>
    </label>
    <label :for="id" v-show="error">{{ textError }}</label>
  </div>
</template>

<script>
export default {
  name: "vInput",
  props: {
    value: {
      type: [String, Number],
      default: ""
    },
    label: {
      type: String,
      default: ""
    },
    error: {
      type: Boolean,
      default: false
    },
    textError: {
      type: String,
      default: ""
    },
    mask: {
      type: String,
      required: false
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    requireInput: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      id: null
    };
  },
  mounted() {
    this.id = this._uid;
  }
};
</script>

<style scoped></style>
